import darkCalendar from '@/components/dark-calendar/dark-calendar.vue';
import huiziDeatil from '@/components/huizi-deatil/huizi-detail.vue';
export default {
	data() {
		return {
			title: 'Hello',
			signeddates: ['2020-4-15', '2020-4-17'],
			huiziData: [],
			huizi: {//模拟会子数据
				total: 10,
				totalPay: 200000,
				totalPayment: 150000,
				totalSurplusPay: 50000,
				totalHuizi: [{//模拟每名会子具体数据
						id: 1,
						projectName: '嘻嘻500',
						model: 'fixed',
						currentHuizi: [{//模拟每名会子每个月具体数据
							id: "1-1",
							currentPeriod: 2,
							actualPayment: 350,
							isPay: true,
							payTime: '2020-1-12'
						}],
						totalPeriods: 50,
						payment: 500,
						isPay: true,
						creationTime: '2020-1-12'
					},
					{
						id: 2,
						projectName: '嘻嘻500',
						model: 'fixed',
						currentHuizi: [{
							id: "2-1",
							currentPeriod: 2,
							actualPayment: 350,
							isPay: true,
							payTime: '2020-1-2'
						}],
						totalPeriods: 50,
						payment: 500,
						isPay: true,
						creationTime: '2020-1-1'
					}
				]
			}
		}
	},
	onLoad() {

	},
	methods: {
		change(e) {
			console.log( e )
			const zz = /^(\d{4})-(\d{1,2})-(\d{1,2})$/
			let currentMonth = Number(  zz.exec( e )[2] ) -1;
			let huiziLen = this.huizi.totalHuizi.length;
			let currentMonthHuizi = [];
			let currentDayHuizi = [];
				for (let i = 0; i < huiziLen; i++) {
					//获取每名会子当月的数据
					currentMonthHuizi.push(this.huizi.totalHuizi[i].currentHuizi[currentMonth]);
				}
				
				let currentMonthHuiziLen = currentMonthHuizi.length;
				for( let i = 0; i < currentMonthHuiziLen; i++){
					
					if( currentMonthHuizi[i].payTime == e ){
						currentDayHuizi.push( currentMonthHuizi[i] )
					}
					
					this.huiziData = currentDayHuizi;
					console.log ( this.huiziData  )
					return true;
				}
				
		}
		
	},
	components: {
		darkCalendar,
		huiziDeatil
	}
}
